@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  padding-top: 10px;
  background: #484B5B repeat;
  height: 100vh;
  box-sizing: border-box;
  color: white;
}

.header,
.content {
  width: 940px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header {
  padding-top: 40px;
  padding-left: 10px;
  flex-wrap: wrap;
}

.table-graph-container {
  border: 1px solid #5D606A;
  box-sizing: border-box;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  margin-left: 150px;
  height: 330px;
  width: 460px;
}

.location {
  margin-bottom: 30px;
  height: 40px;
  display: inline-block;
}

.location .currentLocation {
  cursor: pointer;
}

.location .currentLocation .city {
  font-style: normal;
  font-weight: 800;
  font-size: 21px;
  line-height: 25px;
}

.location .currentLocation .country {
  font-weight: 300;
  font-size: 21px;
  line-height: 25px;
}

.location .currentLocation .changeLocation {
  font-size: 14px;
  line-height: 14px;
  text-decoration: underline;
  color: #9A9A9A;
  margin-top: 1px;
}

.location .locationInputForm .locationInput {
  height: 25px;
  background: none;
  border: none;
  font-weight: 400;
  font-size: 21px;
  line-height: 25px;
  color: white;
  outline: none;
}

.dateSelector {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.dateSelector .dateTimeString {
  line-height: 24px;
  vertical-align: top;
  margin: 0 8px;
  font-size: 18px;
}

.dateSelector .dateTimeArrow {
  cursor: pointer;
}

.content-table {
  width: 980px;
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
}

.table-cell-border {
  border: 1px solid #5D606A;
  border-radius: 10px;
  padding: 10px 0px 10px 15px;
  width: 150px;
  margin: 10px;
}

.temperature-cell {
  text-align: center;
}

.temperature-cell svg {
  display: none;
  top: 13px;
  position: relative;
  height: 64px;
  width: 64px;
}

.temperature-cell .number {
  line-height: 74px;
  font-weight: 300;
  font-size: 39px;
}

.temperature-cell .celsius {
  font-weight: 500;
  font-size: 28px;
  line-height: 51px;
  color: #5D6972;
  vertical-align: top;
}

.icon-cell {
  font-family: 'Roboto', sans-serif;
  padding-top: 21px;
}

.icon-cell .value {
  font-size: 26px;
  line-height: 30px;
  letter-spacing: -0.05em;
}

.icon-cell .sub {
  margin-top: 6px;
}

.icon-cell .sub * {
  display: inline-block;
  vertical-align: top;
}

.icon-cell .sub span {
  font-size: 13px;
  line-height: 21px;
}

.charts h5 {
  font-size: 18px;
  line-height: 22px;
  margin: 14px 0;
}

.charts .tempChart > svg {
  overflow: visible;
}

.charts .rainChanceTitle {
  margin-top: 24px;
}

@media screen and (max-width: 470px) {
  .App {
    padding-top: 3px;
    height: 110vh;
    max-width: 470px;
  }
  .header,
  .content {
    max-width: 375px;
    margin: 0px;
    padding: 3px;
  }
  .header {
    padding: 0px;
    margin: 5px 0 15px 20px;
  }
  .table-graph-container {
    display: none;
  }
  .location {
    margin-bottom: 15px;
  }
  .location .currentLocation {
    cursor: pointer;
  }
  .location .currentLocation .city {
    font-style: normal;
    font-weight: 800;
    font-size: 21px;
    line-height: 25px;
  }
  .location .currentLocation .country {
    font-weight: 400;
    font-size: 24px;
    line-height: 25px;
  }
  .location .currentLocation .changeLocation {
    font-size: 14px;
    line-height: 14px;
    text-decoration: underline;
    color: #9A9A9A;
    margin-top: 1px;
  }
  .dateSelector {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .dateSelector .dateTimeString {
    line-height: 24px;
    vertical-align: top;
    margin: 0 8px;
    font-size: 18px;
  }
  .dateSelector .dateTimeArrow {
    cursor: pointer;
  }
  .content-table {
    width: 370px;
    margin: 0px 0 0 15px;
  }
  .table-cell-border {
    border: 1px solid #5D606A;
    box-sizing: border-box;
    border-radius: 10px;
    padding: 0 5px;
    text-align: center;
    margin: 10px;
    margin-bottom: 15px;
  }
  .temperature-cell {
    text-align: center;
  }
  .temperature-cell svg {
    display: none;
    top: 13px;
    position: relative;
    height: 32px;
    width: 32px;
  }
  .temperature-cell .number {
    line-height: 94px;
    font-weight: 300;
    font-size: 46px;
  }
  .temperature-cell .celsius {
    font-weight: 500;
    font-size: 20px;
    line-height: 51px;
    color: #5D6972;
    vertical-align: top;
  }
  .icon-cell {
    font-family: 'Roboto', sans-serif;
    padding: 15px 0;
  }
  .icon-cell .value {
    font-size: 28px;
    line-height: 35px;
    letter-spacing: -0.05em;
  }
  .icon-cell .sub {
    margin: 7px;
  }
  .icon-cell .sub * {
    display: inline-block;
    vertical-align: top;
  }
  .icon-cell .sub span {
    font-size: 17px;
    line-height: 24px;
  }
}

