  }
  .temperature-cell .celsius {
    font-weight: 500;
    font-size: 20px;
    line-height: 51px;
    color: #5D6972;
    vertical-align: top;
  }
  .icon-cell {
    font-family: 'Roboto', sans-serif;
    padding: 15px 0;
  }
  .icon-cell .value {
    font-size: 23px;
    line-height: 30px;
    letter-spacing: -0.05em;
  }
  .icon-cell .sub {
    margin: 5px;
  }
  .icon-cell .sub * {
    display: inline-block;
    vertical-align: top;
  }
  .icon-cell .sub span {
    font-size: 17px;
    line-height: 21px;
  }
}

@media screen and (max-height: 500px) and (max-width: 470px) {
  .App {
    padding-top: 3px;
    height: 100%;
  }
}

@media screen and (max-height: 400px) and (max-width: 340px) {
  .App {
    padding-top: 3px;
    height: 100%;
  }
}

